import styled from "@emotion/styled";
import {
  MainSectionTitleWrapper,
  MainSectionTitleWrapperInnerBox,
  MainSectionTitle,
  Heading01,
  Heading03_Exclusion,
} from "../typograpy/heading";
import { useContext, useRef, useEffect } from "react";
//GSAP
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
//Text
import { LanguageContext } from "../../App"; //    
//Shape
import { Rectangle_L } from "../shape/rectangle";

const GreetingSection = styled.div`
  width: 100%;
  height: 100vh;
`;

const VideoWrapper = styled.div`
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  /* position: relative; */
  /* z-index: 100; */
  z-index: -999;
`;

const StyledVideo = styled.video`
  width: 100%;
  height: 100vh;
  object-fit: cover;
`;

interface MainGreetingSectionProps {
  isDarkMode: boolean;
}

function MainGreetingSection({ isDarkMode }: MainGreetingSectionProps) {
  const { language } = useContext(LanguageContext); //    
  const videoRef = useRef<HTMLVideoElement>(null);

  //Title Ref 
  const MainSectionTitle_Ref = useRef(null);
  const Heading01_Ref = useRef(null);
  const Heading02_Ref = useRef(null);
  const Rectangle_L_Ref = useRef(null);

  //       
  useEffect(() => {
    // 
    if (videoRef.current) {
      videoRef.current.load(); //  
      gsap.fromTo(
        videoRef.current,
        { scale: 3 },
        { scale: 1, duration: 2.2, ease: "Power3.easeOut" }
      );
    }

    //GSAP 
    gsap.fromTo(
      Heading02_Ref.current,
      { opacity: 0, y: 180 },
      { opacity: 1, y: 0, duration: 2.2, ease: "Power3.easeOut" }
    );
    gsap.fromTo(
      Rectangle_L_Ref.current,
      { opacity: 0, y: 180 },
      { opacity: 1, y: 0, duration: 2.2, ease: "Power3.easeOut" }
    );
    gsap.fromTo(
      Heading01_Ref.current,
      { opacity: 0, y: 180 },
      { opacity: 1, y: 0, duration: 2.2, ease: "Power3.easeOut" }
    );
    gsap.fromTo(
      MainSectionTitle_Ref.current,
      { opacity: 0, y: 180 },
      { opacity: 1, y: 0, duration: 2.2, ease: "Power3.easeOut" }
    );
  }, [isDarkMode]);

  //      .
  const TextData =
    language === "en"
      ? require("../../i18n/en/main_Section.json")
      : require("../../i18n/kr/main_Section.json");

  return (
    <GreetingSection>
      <MainSectionTitleWrapper>
        <MainSectionTitleWrapperInnerBox>
          <Heading03_Exclusion ref={Heading02_Ref} isDarkMode={isDarkMode}>
            {TextData.Greeting.Name}
          </Heading03_Exclusion>
          <Rectangle_L ref={Rectangle_L_Ref} />
          <MainSectionTitle ref={MainSectionTitle_Ref} isDarkMode={isDarkMode}>
            {TextData.Greeting.GreetingTitle}
          </MainSectionTitle>
          <Heading01 ref={Heading01_Ref} isDarkMode={isDarkMode}>
            {TextData.Greeting.Slogan}
          </Heading01>
        </MainSectionTitleWrapperInnerBox>
      </MainSectionTitleWrapper>
      <VideoWrapper>
        <StyledVideo ref={videoRef} autoPlay loop muted>
          <source
            src={
              isDarkMode
                ? `${process.env.PUBLIC_URL}/Videos/Video_02_DarkMode.mp4` //     
                : `${process.env.PUBLIC_URL}/Videos/Video_02_LightMode.mp4` //     
            }
            type="video/mp4"
          />
        </StyledVideo>
      </VideoWrapper>
    </GreetingSection>
  );
}
export default MainGreetingSection;
